<template>
  <div>
    <div v-if="step == 0">
      <Post  v-for="(a, i) in 게시물" :key="i" :게시물="게시물[i]" />
    </div>
   
    <!-- 필터선택페이지 -->
    <div v-if="step == 1">
      <div :class="선택한필터" class="upload-image" :style="{ backgroundImage : `url(${url})`}"></div>
      <div class="filters">
        <FilterBox v-for="a in filterName" :key="a" :url="url" :filterIns="a">
          {{a}}
          <!-- 
            slot의 갯수가 많아지면 props를 쓰는게 낫다.
            <template v-slot:a></template>
            <template v-slot:b></template>
           -->

          <!-- 
            slot props문법
            <template v-slot:defailt="작명">{{작명.msg}}</template>  
          -->
        
        </FilterBox>
       
      </div>
    </div>

    <!-- 글작성페이지 -->
    <div v-if="step == 2">
      <div :class="선택한필터" class="upload-image" :style="{ backgroundImage : `url(${url})`}"></div>
      <div class="write">
        <textarea class="write-box" @input="$emit('write', $event.target.value)">write!</textarea>
      </div>
    </div>
  </div>

  <div v-if="step == 3">
      <MyPage :one="1"/>
  </div>
</template>

<script>
import Post from "./Post";
import FilterBox from "./FilterBox";
import filterName from "../assets/filterName.js";
import MyPage from "./MyPage.vue"

export default {
  name: "Continer",
  data() {
    return {
      filterName : filterName,
      선택한필터 : '',
    }
  },
  components: {
    Post: Post,
    FilterBox: FilterBox,
    MyPage: MyPage,
  },
  props: {
    게시물: Array,
    step: Number,
    url: Object,
  },
  mounted(){
     this.emitter.on('박스클릭함',(a) => {
       this.선택한필터 = a;
    });
  },
};
</script>

<style>
.upload-image {
  width: 100%;
  height: 450px;
  background: cornflowerblue;
  background-size: cover;
}
.filters {
  overflow-x: scroll;
  white-space: nowrap;
}
.filter-1 {
  width: 100px;
  height: 100px;
  background-color: cornflowerblue;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
.filters::-webkit-scrollbar {
  height: 5px;
}
.filters::-webkit-scrollbar-track {
  background: #f1f1f1;
}
.filters::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.write-box {
  border: none;
  width: 90%;
  height: 100px;
  padding: 15px;
  margin: auto;
  display: block;
  outline: none;
}
</style>