<template>
  <div class="header">
    <ul class="header-button-left">
      <li @click="step = 0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 0" @click="step = 3">팔로워</li>
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">업로드</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
     
  </div>
   <!-- 
     <p>{{ $store.state.more }}</p>
     <button @click="$store.dispatch('getData')">더보기 버튼</button> 
   -->
  
    <!-- <h4>안녕 {{$store.state.name}}</h4>
    <h4>안녕 {{$store.state.age}}</h4>
    <button @click="$store.commit('이름변경')">이름변경</button>
    <button @click="나이증가(5)">나이증가</button>  -->
 

 

  <Container @write="작성한글 = $event" :게시물="게시물" :step="step" :url="url" />
  <button v-if="step == 0" @click="more">더보기</button>
  <!-- 
    <p>{{ now2 }} {{ 카운터 }}</p>
    <button @click="카운터++">버튼</button>
  -->
  <div class="footer">
    <ul class="footer-button-plus">
      <input
        @change="upload"
        accept="image/*"
        type="file"
        id="file"
        class="inputfile"
      />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
  <!-- 
    <h4>안녕 {{$store.state.name}}</h4> vuex 문법 가져다 쓰는방법
    <button @click="$store.state.name = 'kimmoonkwan'">버튼</button> -
    이런식으로 데이터가 변경된 vuex국를은 컴포넌트 안에서 직접 수정을 금지한다.
    why? 이런식으로 바꾸면 추적하기 어렵다.
    ************************************************************************
    state가 수정하고 싶으면,
    1. 미리 store.js에 수정방법을 정의해둔다.
    2. 그 방법을 컴포넌트에서 소환해서 수정한다.
    
  ->

  <!- <div v-if="step == 0">내용0</div>
  <div v-if="step == 1">내용1</div>
  <div v-if="step == 2">내용2</div>
  <button @click="step = 0">버튼0</button>
  <button @click="step = 1">버튼1</button>
  <button @click="step = 2">버튼2</button> -->

</template>

<script>
import Container from "./components/Container";
import postdata from "./assets/postdata.js";
import axios from "axios";
import {mapMutations, mapState} from 'vuex';
export default {
  name: "App",
  data() {
    return {
      게시물: postdata,
      더보기: 0,
      step: 0,
      url: "",
      작성한글: "",
      선택한필터: "",
      카운터 : 0,
    };
  },
  mounted(){
     this.emitter.on('박스클릭함',(a) => {
       this.선택한필터 = a;
    });
  },
  components: {
    Container: Container,
  },
  //계산 결과들 저장용 함수라 생각하면 된다.
  //그래서 불러서 쓸 때 () 괄호 붙이면 안된다.
  computed : {
    //methods vs computed
    now2(){
      //사용할 때마다가 아닌 로드 될때 한번 실행되고 값을 간직 한다. 다시 부르면 최초 간직한 값을 퉤 뱉어줌
      return new Date();
    },
    name(){
      return this.$store.state.name;
    },
    ...mapState(['name','age','likes']),
    ...mapState({작명 : 'name'}),
  },
 
  methods: {
    ...mapMutations(['setmore','좋아요','나이증가']),
    now(){
      //사용할 때마다 실행됨
      return new Date();
    },
    more() {
      // axios.post('URL', {name : 'kim'}).then().catch((err) => {
      //   alert(err);
      // })
      axios
        .get(`https://codingapple1.github.io/vue/more${this.더보기}.json`)
        .then((결과) => {
          this.게시물.push(결과.data);
          this.더보기++;
        });
    },
    upload(e) {
      let 파일 = e.target.files;
      let url = URL.createObjectURL(파일[0]);
   
      this.step++;
      this.url = url;
    },
    publish() {
      let 내게시물 = {
        name: "Kim Hyun",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.url,
        likes: 365,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: this.선택한필터
      };
      this.게시물.unshift(내게시물);
      this.step = 0;
      alert("저장완료!");
      //unshift array형에 자료추가
    },
  },
  
};
</script>

