<template>
  <div @click="fire" :class="filterIns" class=filter-item :style="{ backgroundImage : `url(${url})`}">
      <slot></slot>
      <!-- 
          mitt로 데이터 전송법
          함수로 만든다.
       -->
      <!-- <button @click="fire">버튼</button> -->
      <!-- 
        네임슬롯 여러개 보내고 싶을 때
        <slot name = "a"></slot>
        <slot name ="b"></slot> 
      -->
      <!-- 
          부모가 자식의 데이터를 가져다 쓰고 슾이면
          slot props 문법을 쓴다. 잘 쓸 일은 없다는데 쓰일거 같다.
          밑에 data에서 보내줄 데이터 등록
          슬롯에 입력.부모로 넘어감.
          <slot msg="msg"></slot>

      -->
  </div>
</template>

<script>
export default {
    name : 'filterbox',
    data() {
        return {
            msg : 'hello'
            
        }
    },
    props : {
        url: Object,
        filterIns : String,
    },
    methods: {
         
        //   mitt로 데이터 전송법
        //   함수로 만든다.
        // this.emitter.emit()으로 발사.
       
        // fire(){
        //     this.emitter.emit('작명','데이터');
        // }

         fire(){
            this.emitter.emit('박스클릭함', this.filterIns);
        }
    },

};
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>