//vuex기본 설정

import { createStore } from 'vuex';
import axios from 'axios';


const store = createStore({
  state(){
    return {
      name : 'kim',
      age : 20,
      likes : 0,
      flag: false,
      more : {},
    }
  },
  mutations :{
    setMore(state, data){
      state.more = data;
    },
    //데이터 수정 방법을 정의
    이름변경(state){
      state.name = 'park';
    },
    나이증가(state, data){
      state.age += data
    },
    좋아요(state){
      if(state.flag == false){
        state.likes++;
        state.flag = true;
      }else{
        state.likes--
        state.flag = false;
      }
    },
    
  },
  actions : {
    //ajax하는 곳 또는 오래 걸리는 작업들.
    getData(context){
      axios.get('https://codingapple1.github.io/vue/more0.json')
      .then((a) =>{
        console.log(a.data);
        context.commit('setMore',a.data)
      });
    },
  },
})

export default store